 <template>
    <div class="weather-forecast">
      
      <div v-for="day in forecast" :key="day.dt" class="forecast-day">
        <p>{{ $t('forecast.date') }} {{ formatDate(day.dt) }}</p>
        <p>{{ $t('forecast.temperature') }}: {{ convertTemperature(day.main.temp) }} {{ unitSymbol }}</p>
        <p>{{ $t('forecast.condition') }}: {{ day.weather[0].description }}</p>
        <img :src="`https://openweathermap.org/img/wn/${day.weather[0].icon}@2x.png`" :alt="$t('forecast.condition')" />
        <p>{{ $t('forecast.humidity') }}: {{ day.main.humidity }}%</p>
        <p>{{ $t('forecast.pressure') }}: {{ day.main.pressure }} hPa</p>
        <p>{{ $t('forecast.wind') }}: {{ day.wind.speed }} м/с, {{ getWindDirection(day.wind.deg) }}</p>
      </div>
    </div>
  </template>

  <script>
  export default {
    props: {
      forecast: Array,
      getWindDirection: Function,
      unit: String // Принимаем выбранную единицу измерения
    },
    computed: {
      unitSymbol() {
        return this.unit === 'celsius' ? '°C' : '°F';
      }
    },
    methods: {
      formatDate(timestamp) {
        return new Date(timestamp * 1000).toLocaleDateString();
      },
      convertTemperature(temp) {
        return this.unit === 'celsius' ? temp : (temp * 9/5 + 32);
      }
    }
  }
  </script>

  <style scoped>
  .weather-forecast {
    margin-top: 20px;
  }

  .forecast-day {
    border-top: 1px solid #ddd;
    padding: 10px 0;
  }

  .forecast-day:first-child {
    border-top: none;
  }
  </style>