<template>
  <div id="app">
    <h1>{{ $t('weatherApp') }}</h1>

    <!-- Выбор языка -->
    <div>
      <label for="language">{{ $t('language') }}</label>
      <select id="language" v-model="currentLanguage" @change="changeLanguage">
        <option value="en">English</option>
        <option value="ru">Русский</option>
      </select>
    </div>

    <span>{{ $t('selectCity') }}</span><br>
    <select @change="refresh($event.target.value)" v-model="selectedCityId">
      <option value="520494">Нижний Тагил</option>
      <option value="1511954">Алапаевск</option>
      <option value="524901">Москва</option>
      <option v-for="city in favoriteCities" :key="city.id" :value="city.id">{{ city.name }}</option>
    </select>

    <!-- Добавление города в избранное -->
    <button @click="addFavoriteCity(weatherData.city)" v-if="weatherData">{{ $t('addToFavorites') }}</button>

    <!-- Список избранных городов -->
    <div v-if="favoriteCities.length > 0">
      <h3>{{ $t('favoriteCities') }}</h3>
      <ul>
        <li v-for="city in favoriteCities" :key="city.id">
          {{ city.name }}
          <button @click="removeFavoriteCity(city.id)">{{ $t('remove') }}</button>
        </li>
      </ul>
    </div>

    <!-- Переключатель между Цельсием и Фаренгейтом -->
    <div>
      <label for="unit">{{ $t('unit') }}</label>
      <select id="unit" v-model="unit">
        <option value="celsius">°C</option>
        <option value="fahrenheit">°F</option>
      </select>
    </div>

    <div v-if="weatherData" class="weather-info">
      <h2>{{ $t('weatherApp') }} - {{ weatherData.city.name }}</h2>
      <p>{{ $t('temperature') }}: {{ convertTemperature(weatherData.list[0].main.temp) }} {{ unitSymbol }}</p>
      <p>{{ $t('condition') }}: {{ weatherData.list[0].weather[0].description }}</p>
      <img :src="`https://openweathermap.org/img/wn/${weatherData.list[0].weather[0].icon}@2x.png`" alt="Weather Icon" />
      <p>{{ $t('humidity') }}: {{ weatherData.list[0].main.humidity }}%</p>
      <p>{{ $t('pressure') }}: {{ weatherData.list[0].main.pressure }} hPa</p>
      <p>{{ $t('wind') }}: {{ weatherData.list[0].wind.speed }} м/с, {{ getWindDirection(weatherData.list[0].wind.deg) }}</p>
      <h3>{{ $t('weatherForecast') }}</h3>

      <!-- Подключение компонента прогноза -->
      <WeatherForecast :forecast="forecastData" :getWindDirection="getWindDirection" :unit="unit" v-if="forecastData"/>
    </div>
    <p v-else>{{ $t('loading') }}</p>
  </div>
</template>

<script>
import WeatherForecast from './components/WeatherForecast.vue';

export default {
  name: 'App',
  components: {
    WeatherForecast
  },
  data() {
    return {
      weatherData: null,
      forecastData: null,
      unit: 'celsius', // по умолчанию Цельсий
      currentLanguage: 'ru', // Язык по умолчанию
      favoriteCities: [], // Список избранных городов
      selectedCityId: '' // ID выбранного города
    };
  },
  computed: {
    unitSymbol() {
      return this.unit === 'celsius' ? '°C' : '°F';
    }
  },
  watch: {
    unit() {
      if (this.weatherData) {
        this.refresh(this.weatherData.city.id);
      }
    },
    favoriteCities: {
      handler(newCities) {
        localStorage.setItem('favoriteCities', JSON.stringify(newCities));
      },
      deep: true
    },
    selectedCityId(newCityId) {
      if (newCityId) {
        this.refresh(newCityId);
        localStorage.setItem('selectedCityId', newCityId);
      }
    }
  },
  methods: {
    async refresh(cityId) {
      this.weatherData = null;
      this.forecastData = null;

      const apiKey = '2ad80693f04a2f391f2e7861b0836e07';

      const response = await fetch(`https://api.openweathermap.org/data/2.5/forecast?id=${cityId}&units=metric&lang=ru&appid=${apiKey}`);
      const data = await response.json();

      this.weatherData = data;
      this.forecastData = this.getDailyForecast(data.list);
    },
    getWindDirection(deg) {
      const directions = ['С', 'СВ', 'В', 'ЮВ', 'Ю', 'ЮЗ', 'З', 'СЗ'];
      return directions[Math.round(deg / 45) % 8];
    },
    getDailyForecast(list) {
      const dailyForecast = list.filter(item => item.dt_txt.includes("12:00:00"));
      return dailyForecast;
    },
    convertTemperature(temp) {
      return this.unit === 'celsius' ? temp : (temp * 9/5 + 32);
    },
    // Функция для добавления города в избранное
    addFavoriteCity(city) {
      if (!this.favoriteCities.some(favCity => favCity.id === city.id)) {
        this.favoriteCities.push(city);
      }
    },
    // Функция для удаления города из избранного
    removeFavoriteCity(cityId) {
      this.favoriteCities = this.favoriteCities.filter(city => city.id !== cityId);
    },
    // Загрузка избранных городов из localStorage при монтировании компонента
    loadFavoriteCities() {
      const savedCities = JSON.parse(localStorage.getItem('favoriteCities')) || [];
      this.favoriteCities = savedCities;
    },
    // Функция для изменения языка
    changeLanguage() {
      this.$i18n.locale = this.currentLanguage;
    },
    // Загрузка сохранённого города из localStorage при монтировании компонента
    loadSelectedCity() {
      const savedCityId = localStorage.getItem('selectedCityId');
      if (savedCityId) {
        this.selectedCityId = savedCityId;
      } else {
        this.selectedCityId = '520494'; // Город по умолчанию
      }
    }
  },
  mounted() {
    this.loadFavoriteCities(); // Загружаем избранные города
    this.loadSelectedCity(); // Загружаем сохранённый город
    this.refresh(this.selectedCityId); // Загружаем погоду для выбранного города
  }
}
</script>

